* {
  margin: 0;
  padding: 0;
}

body {
  margin: 50px;
  background-color: white;
}

header {
  margin-bottom: 50px;
}

h1, h2, h3 {
  font-family: Helvetica, sans-serif;
  letter-spacing: -0.5pt;
}

h1, h2 {
  font-size: 14pt;
}

h3 {
  font-size: 32pt;
}

h2 {
  margin-bottom: 20px;
}

#alert {
  font-size: 32pt;
}

p, li, a {
  font-family: "Inconsolata", monospace;
  font-size: 14pt;
  letter-spacing: -0.6pt;
  text-transform: lowercase;
}

ul {
  max-width: 900px;
}

li {
  list-style: none;
  line-height: 1em;
  padding: 10px 0px 10px 0px;
  border-bottom: 1px solid black;
}

a {
  text-decoration: none;
  color: blue;
}

a:hover {
  color:darkslategrey;
}

footer {
  margin: 10px 0px;
  color:silver;
}

footer p {
  font-size: 10pt;
}